.main-header {
  background-color: #00b2ff;
  background-image: image-set("../img/background/background-header-desktop@2x.webp" 2x, "../img/background/background-header-desktop.webp" 1x);
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-header {
    background-image: image-set("../img/background/background-header-tablet@2x.webp" 2x, "../img/background/background-header-tablet.webp" 1x);
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .main-header {
    background-image: image-set("../img/background/background-header-mobile@2x.webp" 2x, "../img/background/background-header-mobile.webp" 1x);
  }
}

.main-header__nav {
  display: flex;
  justify-content: space-between;
}

.main-header__toogle {
  display: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main-header__toogle {
    display: block;
    border: none;
    background-color: inherit;
    padding-top: 10px;
  }
}

@media (max-width: 767px) {
  .nojs .main-header__toogle {
    display: none;
  }
}

.main-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-header__list a {
  font-weight: 500;
  line-height: 20px;
  color: #ffffff;
}

@media (max-width: 767px) {
  .main-header__nav--opened .navigation {
    display: grid;
    grid-template-columns: 277px;
    flex-direction: column;
    position: absolute;
    top: 67px;
    right: 0;
    overflow: scroll;
    height: auto;
    max-height: 490px;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .main-header__nav--opened a {
    color: #000000;
  }
}

@media (max-width: 767px) {
  .main-header__nav--closed .navigation {
    display: none;
  }
}

.main-header__intro {
  color: #ffffff;
}

/* .navigation {

} */
.navigation ul {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.featuries {
  background-color: #e3ecf6;
  background-image: image-set("../img/background/background-featuries@2x.webp" 2x, "../img/background/background-featuries.webp" 1x);
  background-repeat: no-repeat;
  background-size: cover;
}

.featuries ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.featuries li {
  background-color: #f2f5f7;
}

.catalog ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .catalog ul {
    grid-template-columns: repeat(2, 1fr);
  }
}

.product {
  position: relative;
  min-height: 386px;
  width: 100%;
  overflow: hidden;
  color: #f2f5f7;
}

.product:hover .product__description {
  display: flex;
  z-index: 2;
  position: absolute;
  top: 0;
  bottom: 0;
  justify-content: space-between;
}

.product__inner {
  z-index: -1;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.product__inner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product__inner h3 {
  position: absolute;
  bottom: 22px;
  left: 30px;
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  color: #f2f5f7;
  z-index: 1;
}

.product__description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  flex-direction: column;
  padding-left: 25px;
  padding-right: 15px;
  background-color: #011c40;
}

.product__description dd {
  margin: 0;
  padding: 0;
}

.product__button {
  width: 100%;
  background-color: #f2f5f7;
}

.booking {
  background-color: #e3ecf6;
  background-image: image-set("../img/background/background-booking-desktop@2x.webp" 2x, "../img/background/background-booking-desktop.webp" 1x);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 1023px) {
  .booking {
    background-image: image-set("../img/background/background-booking-tablet@2x.webp" 2x, "../img/background/background-booking-tablet.webp" 1x);
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .booking {
    background-image: image-set("../img/background/background-booking-mobile@2x.webp" 2x, "../img/background/background-booking-mobile.webp" 1x);
  }
}

.booking__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .booking__wrapper {
    flex-direction: column;
  }
}

.booking__form {
  background-color: #011c40;
  color: #f2f5f7;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 44px 50px 50px;
}

input {
  margin-bottom: 20px;
  padding: 11px 16px;
  font-size: 14px;
  line-height: 21px;
  border: none;
  background-color: #f2f5f7;
}

.contacts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.contacts__description ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-footer {
  background-color: #011c40;
  color: #f2f5f7;
}

.main-footer__wrapper {
  display: flex;
}

.main-footer__socials {
  margin-left: auto;
}

.main-footer__socials ul {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

